<template>
  <div class="container">
    <div class="container-form">
      <el-form ref="qryParamRef" :model="qryParam" size="small">
        <div class="row-container">
          <div class="left-row">
            <el-row :gutter="20" type="flex" style="flex-wrap: wrap">
              <el-col :span="6">
                <el-form-item :label="$t('accountMag.account')" prop="userCode">
                  <el-input
                    v-model="qryParam.userCode"
                    :placeholder="inputPh('accountMag.account')"
                    disabled
                  ></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item :label="$t('oper.operName')" prop="operName">
                  <el-input
                    v-model="qryParam.operName"
                    :placeholder="inputPh('oper.operName')"
                  ></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item :label="$t('oper.operDate')" prop="operDateBegin">
                  <ct-date-picker
                    :begin.sync="qryParam.operDateBegin"
                    :end.sync="qryParam.operDateEnd"
                  >
                  </ct-date-picker>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item :label="$t('oper.resultCode')" prop="resultCode">
                  <ct-select
                    v-model="qryParam.resultCode"
                    :placeholder="selectPh('oper.resultCode')"
                    :options="resultCodeOptions"
                    filterable
                  >
                  </ct-select>
                </el-form-item>
              </el-col>
              <el-col v-show="powerSearch" :span="6">
                <el-form-item :label="$t('oper.clientIp')" prop="clientIp">
                  <el-input
                    v-model="qryParam.clientIp"
                    :placeholder="inputPh('oper.clientIp')"
                  ></el-input>
                </el-form-item>
              </el-col>
            </el-row>
          </div>
          <div class="right-row">
            <el-form-item label-width="0">
              <div class="search-button-group">
                <el-button
                  type="primary"
                  icon="el-icon-search"
                  @click="qryCmpParamPage(false)"
                  >{{ $t('common.query') }}</el-button
                >
                <el-button icon="el-icon-refresh" @click="reset">{{
                  $t('common.reset')
                }}</el-button>
                <show-more-button v-model="powerSearch" />
              </div>
            </el-form-item>
          </div>
        </div>
      </el-form>
      <ct-table
        ref="table"
        v-loading="loading"
        :data="tableData"
        :border="true"
        :pagination="pagination"
        :total="total"
        @change="tableChange"
        @selection-change="handleSelectionChange"
      >
        <el-table-column prop="operDate" :label="$t('oper.operDate')" min-width="110">
        </el-table-column>
        <el-table-column
          prop="operName"
          :label="$t('oper.operName')"
          show-overflow-tooltip
          min-width="110"
        ></el-table-column>
        <el-table-column
          prop="userCode"
          :label="$t('oper.userCode')"
          show-overflow-tooltip
          min-width="110"
        >
        </el-table-column>
        <el-table-column
          prop="clientIp"
          :label="$t('oper.clientIp')"
          show-overflow-tooltip
          min-width="110"
        ></el-table-column>
        <el-table-column
          prop="traceId"
          :label="$t('oper.traceId')"
          show-overflow-tooltip
          min-width="110"
        ></el-table-column>
        <el-table-column
          prop="resultCode"
          :label="$t('oper.resultCode')"
          show-overflow-tooltip
          min-width="110"
        >
          <template slot-scope="scope">
            <span v-if="scope.row.resultCode == 0"> {{ $t('oper.success') }} </span>
            <span v-if="scope.row.resultCode == 1"> {{ $t('oper.fail') }} </span>
          </template>
        </el-table-column>
        <!-- <el-table-column
          prop="createDate"
          label="创建时间"
          show-overflow-tooltip
          min-width="110"
        >
        </el-table-column>
        <el-table-column :label="$t('common.action')" fixed="right" min-width="110">
          <template slot-scope="scope">
            <div class="action-button-group">
              <el-button v-compPriv="39" type="text" @click="modify(scope.row)"
                >下载</el-button
              >
            </div>
          </template>
        </el-table-column> -->
      </ct-table>
    </div>
  </div>
</template>
<script>
import langMixin from '@/mixins/langMixin'
import fetch from '@/request/fetch'
export default {
  mixins: [langMixin],
  data() {
    return {
      powerSearch: false,
      loading: false,
      tableData: [],
      typeOptions: [],
      qryParam: {
        userCode: this.$store.state.user.user.userCode,
      },
      pagination: {
        pageIndex: 1,
        pageSize: 10,
      },
      total: 0,
      pickerOptions: {
        disabledDate(time) {
          return (
            time.getTime() < Date.now() - 30 * 24 * 60 * 60 * 1000 ||
            time.getTime() > Date.now()
          )
        },
      },
    }
  },
  computed: {
    resultCodeOptions() {
      return [
        {
          value: 0,
          label: this.$t('oper.success'),
        },
        {
          value: '1',
          label: this.$t('oper.fail'),
        },
        {
          value: '',
          label: this.$t('oper.all'),
        },
      ]
    },
  },
  mounted() {
    this.qryCmpParamPage(false)
  },
  methods: {
    // 查询业务配置
    async qryCmpParamPage(pageChangeFlag) {
      if (!pageChangeFlag) {
        this.pagination.pageIndex = 1
        this.pagination.pageSize = 10
      }
      const param = {
        ...this.qryParam,
        ...this.pagination,
      }
      this.loading = true
      const res = await fetch('/operLog/queryOperLogPage', param, {
        method: 'post',
      }).finally(() => {
        this.loading = false
      })
      this.tableData = res.list || []
      this.total = res.total
    },
    tableChange(pagination) {
      this.pagination = pagination
      this.qryCmpParamPage(true)
    },
    handleSelectionChange(val) {
      this.multipleSelection = val
    },
    selectCust(data) {
      this.selectCustomerVisible = false
      this.qryParam.custId = data.custId
      this.qryParam.custName = data.custName
    },

    // 修改
    modifyParam(value) {
      if (value) {
        this.qryCmpParamPage(false)
      }
    },
    clickDelete() {},
    // 重置
    reset() {
      this.$refs.qryParamRef.resetFields()
      this.$nextTick(() => this.qryCmpParamPage(false))
    },
  },
}
</script>
<style lang="scss" scoped>
@import '@/assets/css/page.scss';
</style>
